<template>
  <div class="container">
    <h2 class="text-center">我心底的話</h2>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.push('/')" v-if="this.$store.state.account.ident === ''">
      返回登入
    </button>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.push('/examLobby')" v-if="this.$store.state.account.ident !== ''">
      返回大廳
    </button>
    <div class="row">
      <div class="col-12 input-group mb-2">
        <span class="input-group-text">留言</span>
        <textarea class="form-control" v-model="textarea" rows="5" v-if="this.$store.state.account.ident !== ''"></textarea>
        <textarea class="form-control" v-model="textarea" rows="5" disabled placeholder="你必須先登入後，才能留言。" v-else></textarea>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-success btn-lg" type="button" @click="sendText()" v-if="this.$store.state.account.ident !== ''">送出</button>
      </div>
      <div class="col-12 my-3">
        <p v-if="discussionData.length === 0" class="text-danger fw-bold fs-5 text-center">目前尚未有人留言。</p>
        <ul v-else class="list-group">
          <li v-for="item in discussionData" class="list-group-item d-flex justify-content-between align-items-start">
            <div class="ms-2 me-auto">
              <div class="fw-bold">{{ item.name }}</div>
              {{ item.content }}
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";

export default {
  name: "Discussion",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: -1,
      textarea: "",
      discussionData: [],
    };
  },
  methods: {
    sendText() {
      if (this.textarea.trim() !== "") {
        this.$http.post("api/discussion/add", { content: this.textarea }).then((res) => {
          if (res.data.code === 200) {
            // 更新留言資料
            this.discussionData.reverse().push({
              name: this.$store.state.account.name,
              content: this.textarea,
            });
            this.discussionData.reverse();
            this.textarea = "";
          } else {
            console.log(res.data);
          }
        });
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.funcNum = 1;
        this.MSG.TYPE = "primary"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["留言成功。"]; // 內容
      } else {
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.funcNum = 1;
        this.MSG.TYPE = "warning"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["請輸入留言內容。"]; // 內容
      }
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  mounted() {
    // 取得留言資料
    this.$http.get("api/discussion/getDiscussList").then((res) => {
      if (res.data.code === 200) {
        this.discussionData = res.data.data.reverse();
      } else {
        console.log(res.data);
      }
    });
  },
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === 1) {
            this.hideAlert();
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
  },
};
</script>

<style lang="scss"></style>
